:root {
    --background-color: #c9d6df;
    --sub-background-color: #f0f5f9;
    --text-color: #1e2022;
    --shadow-color: rgba(0,0,0,0.25);
}
@font-face {
    font-family: 'LiberationSans';
    src: url('./LiberationSans.ttf') format('truetype');
}
body {
    background-color: var(--background-color);
    color: var(--text-color);
    font-size:2vh;
    transition: background-color 0.5s;
    margin: 0;
    font-family: "LiberationSans";
}
.fishLists {
    margin-top: 15vh;
}
.fishList {
    display:flex;
    flex-direction: column;
    background-color: var(--sub-background-color);
    padding-bottom:3vh;
    padding-top:3vh;
    border-radius: 1vh;
    transition: 0.5s;
    width:80vw;
    margin:auto;
    justify-content: flex-start;
    align-content: center;
    overflow: hidden;
    margin-top:3vh;
    
    -webkit-box-shadow: 0px 10px 5px 0px var(--shadow-color);
    -moz-box-shadow: 0px 10px 5px 0px var(--shadow-color);
    box-shadow: 0px 10px 5px 0px var(--shadow-color);
}
.listHeader {
    display: flex;
    height: 3vh;
    align-items: center;
    margin-top: 0;
    padding-bottom:3vh;

    -webkit-box-shadow: 0px 5px 5px 0px var(--shadow-color);
    -moz-box-shadow: 0px 5px 5px 0px var(--shadow-color);
    box-shadow: 0px 5px 5px 0px var(--shadow-color);
}
.listName {
    text-align: center;
    width:95vw;
    font-size:3vh;
}
.fishes {
    display:flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top:3vh;
}
.fish {
    width:20vw;
    height:20vh;
    background-color: var(--element-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding:2vh;
    border-radius: 3vh;
    margin-top:1vh;
    
    -webkit-box-shadow: 0px 0px 10px 3px var(--shadow-color);
    -moz-box-shadow: 0px 0px 10px 3px var(--shadow-color);
    box-shadow: 0px 0px 10px 3px var(--shadow-color);
    
}
.fishImg {
    margin-top:1%;
    width:90%;
    height:90%;
    border-radius: 1vh;
}
.fishName {
    text-align: center;
    padding:0.5vh;
}
.showButton {
    width:10vh;
    margin-left:1vw;
    background-color: var(--background-color);
    color: var(--text-color);
    height:5vh;
    font-size:2vh;
    
}
.header {
    height:12vh;
    background-color: var(--sub-background-color);
    margin-bottom: 2vh;
    display:flex;
    align-items: center;
    transition: 0.5s;
    position:fixed;
    top:0;
    width:100vw;

    -webkit-box-shadow: 0px 10px 5px 0px var(--shadow-color);
    -moz-box-shadow: 0px 10px 5px 0px var(--shadow-color);
    box-shadow: 0px 10px 5px 0px var(--shadow-color);
}
.siteTitle {
    font-size: 5vh;
    margin-left:1vw;
}




.sliderContainer {
    display:flex;
    flex-direction: column;
    width:10vh;
    position:absolute;
    top:1vh;
    right:3vh;
    justify-items: center;
}
.sliderLabel {
    width:10vh;
    text-align: center;
}
.sliderBackground {
    background-color: var(--background-color);
    width:10vh;
    height:5vh;
    display:flex;
    align-items: center;
    border-radius: 4vh;
    cursor: pointer;
}
.sliderCircle {
    width:3vh;
    height:3vh;
    background-color:var(--text-color);
    border-radius: 5vh;
    transition: 0.5s;
}